import React, {Component} from 'react';
import * as actions from './actions/userActions';
import store from './stores/usersStore'
import * as db from './database/dbinfo';

//styles
import './App.less';
// import styles from './Modules.css';

class App extends Component {

  componentWillMount(){
    console.log('App component');
  }

  login(){
    actions.login();
  }

  logout(){
    actions.logout();
  }

  currentUser(){
    console.log('uid', db.auth.currentUser.uid);
  }

  registerCurrentUser(){
    actions.registerCurrentUser()
  }

  uploadFile(e){
    console.log(e.target.files[0])
    let file = e.target.files[0];
    actions.uploadFile(file)
  }

  render() {
    return (
      <div className="App" >
        <h3>app</h3>
        <button onClick={this.login}>Login</button>
        <button onClick={this.logout}>Logout</button>
        <button onClick={this.currentUser}>current user</button>
        <button onClick={this.registerCurrentUser}>register current user</button>

        <br />
        <input type="file"  onChange={this.uploadFile} />
      </div>
    )
  }
}

export default App;



